import React,{useState, useEffect} from "react"
import {getLastPosts, getAccess, getLastPostsCheminots} from "../API/index"

import ArticleList from "./ArticleList";
const Home = () => {

    const [posts, setPosts] = useState([])

    const getPosts = async() => {
        let res;
        if(getAccess(true)){
            res = await getLastPostsCheminots()
        }else{
            res = await getLastPosts()
        }
        setPosts(res.data)
    }

    useEffect(() => {
        getPosts()
    },[])

    return(
        <>
        <img src="banner-mobile.png" id="banner-mobile"/>
        <img src="banner.png" id="banner-desktop"/>
        <div className="title_block">
            <h2>Présentation</h2>
        </div>
        <div className="article" style={{marginTop: '2vw', marginBottom: '2vw'}}>
            <p>
            En agriculture bio, et encore plus en perma culture, le ver de terre est surnommé "le copain" pour les bienfaits qu'il apporte à la terre.<br/>
            Ce collectif est dédié à tous les copains de la terre qui se déplacent en TER par choix ou par contrainte, mais qui contribuent à limiter les rejets en CO2 sur la planète.<br/>
            Plus largement "Les verts du TER" c'est une philosophie de vie ,un art de consommer, de manger, de se déplacer plus propre, plus responsable.<br/>
            Que vous soyez un nomade pour le travail, un visiteur d'un jour dans notre région, ou un professionnel du rail, vous y trouverez quelque chose.<br/>
            Et puisqu'en 2020, la vie nous a donné encore des leçons, pourquoi ne pas essayer de "changer" nos politiques, notre organisation du transport, pour de meilleurs voyages.<br/>
            Nos dirigeants sont devenus écolos. Tant mieux!
            Avec les verts de la première heure, et les convertis toujours plus nombreux, nous représentons une force qu'ils ne pourront pas ignorer.<br/>
            <br/>
            </p>
            <h3>Alors "Attention départ imminent"...</h3>
        </div>
        <div className="title_block">
            <h2>Dernières actus</h2>
        </div>
        {
            posts.map(({titre, description, rubrique, id, gallerie}) => {
                return (
                    <ArticleList 
                        titre={titre} 
                        description={description} 
                        rubrique={rubrique} 
                        id={id}
                        gallerie={gallerie}
                        link={`/${rubrique && rubrique.nom}/${id}`}
                    />
                )
            })
        }
        </>
    )
}
export default Home